body {
  margin: 0 auto;
  background-color: #66a6ff;
  font-family: 'roboto';
}

*, *::before, *::after {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  color: #fefefe;
  margin: 0 auto;
  text-align: center;
  height: 100vh;
  width: 100%;
  max-width: 1000px;
  background: #0042ab;
}

header {
  padding: 1vw 1vw;
  margin-top: 1.5vw;
  height: 200px;
}

header h1 {
  font-size: 4em;
}

.to-do-form {
  color: #002f7a;
  height: 300px;
  background: #66a6ff;
  display: grid;
  justify-content: center;
  align-items: center;
  border-right: 2px solid #002f7a;
  border-left: 2px solid #002f7a;
}

.to-do-form input[type="text"] {
  border: none;
  border-bottom: 1px solid black;
  background-color: transparent;
  font-size: 2.2em;
  max-width: 500px;
}

.to-do-form label {
  margin-right: 2vw;
  font-size: 3em;
}

.inside-form .submit-btn {
  width: 100%;
  font-size: 1.5em;
  border: 1px solid darkblue;
  background-color: #9cc1ff;
  height: 40px;
  cursor: pointer;
  margin-top: 1vw;
}

.inputs {
  text-align: left;
  margin: 0 auto;
  width: 100%;
  max-width: 700px;
}

.single-input {
  margin: 20px 0;
  display: grid;
  justify-content: center;
  align-items: center;
}

.single-input.first {
  grid-template-columns: 200px 500px;
}

.single-input.second {
  grid-template-columns: 200px 200px 300px;
}

.input-number {
  position: relative;
  height: 30px;
  width: 60%;
  box-sizing: border-box;
  min-width: 129px;
}

.input-number input[type="number"] {
  text-align: center;
  border: none;
  border-bottom: 1px solid black;
  background: transparent;
  font-size: 18px;
  width: 180px;
  font-size: 2em;
  box-sizing: inherit;
  padding-left: 15px;
}

.input-number input[type="button"] {
  font-size: 20px;
  position: absolute;
  background: #fefefe;
  background-color: #9cc1ff;
  border: none;
  width: 40px;
  height: 40px;
  border: 1px solid purple;
  cursor: pointer;
  padding: 0;
  top: 2px;
  z-index: 2;
}

.input-number input[type="button"]:active {
  background: #b6d1ff;
}

.input-number input[type="button"]:first-child {
  left: 0;
}

.input-number input[type="button"]:last-child {
  right: -50px;
}

select {
  max-width: 300px;
  height: 40px;
  text-align: center;
  margin-top: 12px;
  background-color: #9cc1ff;
  border: none;
  border: 1px solid darkblue;
  font-size: 1.5em;
}

.nav-grid {
  width: 100%;
  height: 60px;
  font-size: 1.9em;
  display: grid;
  grid-template-columns: calc(50% - 1px) 2px calc(50% - 1px);
  background-color: #9cc1ff;
  margin: 0 auto;
  cursor: pointer;
  border: 2px solid darkblue;
}

.line {
  background-color: darkblue;
  height: 60px;
}

.nav-option {
  display: grid;
  align-items: center;
  justify-content: center;
  height: 56px;
  -webkit-transition: background-color .2s ease-in;
  transition: background-color .2s ease-in;
}

.nav-option.active {
  background-color: #2c2fc9;
}

ul {
  padding: 0;
  max-height: 380px;
  overflow-y: auto;
  overflow-x: hidden;
}

.list-of-tasks-empty {
  padding-top: 5vw;
  font-size: 2em;
  font-family: 'roboto';
}

.li-item {
  padding: 0 1vw;
  display: grid;
  grid-template-columns: 1fr 200px 150px 60px 60px;
  grid-gap: 20px;
  gap: 20px;
  width: 100%;
  background: lightblue;
  height: 60px;
  color: darkblue;
  justify-content: center;
  font-size: 1.5em;
  align-items: center;
  border-top: 3px solid #000;
  margin-bottom: 35px;
  -webkit-transition: -webkit-transform .7s ease-in;
  transition: -webkit-transform .7s ease-in;
  transition: transform .7s ease-in;
  transition: transform .7s ease-in, -webkit-transform .7s ease-in;
}

.li-item.move-right {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.inside-image {
  cursor: pointer;
}

.inside-image img {
  width: 40px;
}

.img-and-text {
  display: grid;
  grid-template-columns: 30px calc(100% - 30px);
  align-items: center;
  justify-content: center;
}

.img-and-text img {
  width: 30px;
}

